import { imageShowUrl } from "../../../../services/BaseUrl";
import moment from "moment";
import style from "./Activity.module.css";
import { useNavigate, useParams } from "react-router-dom";
import { useTranslation } from "react-i18next";
import CreateButton from "../../../../optimized/Ui/Button/CreateButton";
import { useState } from "react";
import { useStudentInternalEvaluationStartTest } from "../../../../optimized/Hooks/ApiHooks/SubjectManager/subject-manager-api";
import dayjs from "dayjs";
import QvipleLoader from "../../../../optimized/Loader/QvipleLoader";
import { Notfication } from "../../../../validation/Snackbar";
const SubjectTeacherInternalEvaluationMcq = ({ activity }) => {
  const params = useParams();
  const navigate = useNavigate();
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [notificationState, setNotificationState] = useState({
    msg: "",
    run: false,
  });
  const [studentInternalEvaluationStartTest] =
    useStudentInternalEvaluationStartTest();
  const onNavigate = () => {
    if (activity?.student_testset) {
      setDisabled((pre) => !pre);
      let dt = new Date();
      studentInternalEvaluationStartTest({
        stid: activity?.student_testset,
        bodyParameter: {
          date: dayjs(dt).format("YYYY-MM-DD"),
          time: `${dayjs(dt).format("hh:mm a")}`,
        },
      })
        .then((res) => {
          setDisabled((pre) => !pre);
          if (res?.data?.test_access) {
            navigate(`/q/${params.username}/activity/evaluation/mcq`, {
              state: {
                tsid: activity?.student_testset,
                student_testset: activity?.student_testset,
                testset: activity?.testset,
                sid: activity?.notifyPublisher,
                notifyId: activity?._id,
                extraData: res?.data?.stu_test,
              },
            });
          } else {
            setNotificationState({
              msg: t("internal_evaluation_test_is_not_started_yet"),
              run: true,
            });
          }
        })
        .catch({});
    }
  };
  return (
    <>
      <div
        className={
          activity?.notifyReadStatus === "Unread"
            ? `${style.item} ${style.item_active}`
            : style.item
        }
      >
        <div className={style.eachItem}>
          <div className={style.eachItemleft}>
            <img
              src={
                activity?.notifyBySubjectPhoto?.subject_cover
                  ? `${imageShowUrl}/${activity?.notifyBySubjectPhoto?.subject_cover}`
                  : "/images/ins_default_profile.svg"
              }
              alt="student avatar"
            />
            <div>
              <p>{activity?.notifyContent}</p>

              {activity?.student_feedback_status === "Submitted" ? (
                <CreateButton
                  label={t("submitted")}
                  customStyle={{
                    margin: "0.2rem 0 0 0",
                    padding: "0.1rem 0.7rem",
                    width: "fit-content",
                  }}
                  onAction={() => {}}
                />
              ) : (
                <CreateButton
                  label={t("start_test")}
                  customStyle={{
                    margin: "0.2rem 0 0 0",
                    padding: "0.1rem 0.7rem",
                    width: "fit-content",
                  }}
                  onAction={onNavigate}
                />
              )}
            </div>
          </div>
          <div className={style.text}>
            <p>{moment(activity?.notifyTime).format("LT")}</p>
          </div>
        </div>
      </div>

      {disabled && <QvipleLoader />}
      <Notfication
        msg={notificationState.msg}
        run={notificationState.run}
        setRun={() => setNotificationState({ msg: "", run: false })}
        postiton="bottom"
        type="error"
      />
    </>
  );
};

export default SubjectTeacherInternalEvaluationMcq;
